import React, { useState, useRef, useEffect } from 'react';
import { useChat } from '@/contexts/ChatContext';
import { useAuth } from '@/contexts/AuthContext';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Badge } from '@/components/ui/badge';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { cn } from '@/lib/utils';
import { Send, Users, Plus, X, Copy } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import type { ChatMessage, ChatUser } from '@/types';

// Chat Message component
interface MessageProps {
  message: ChatMessage;
  isCurrentUser: boolean;
}

const ChatMessage: React.FC<MessageProps> = ({ message, isCurrentUser }) => {
  const time = new Date(message.timestamp).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
  
  return (
    <div className={cn(
      'flex mb-2',
      isCurrentUser ? 'justify-end' : 'justify-start'
    )}>
      <div className={cn(
        'max-w-[80%] rounded-lg px-3 py-2',
        isCurrentUser 
          ? 'bg-lofi-purple text-white' 
          : 'bg-black/40 border border-lofi-purple/30 text-white'
      )}>
        {!isCurrentUser && (
          <div className="text-xs text-lofi-purple mb-1 font-semibold">
            {message.username}
          </div>
        )}
        <div className="break-words">{message.text}</div>
        <div className="text-xs opacity-70 text-right mt-1">{time}</div>
      </div>
    </div>
  );
};

// Party Users List component
const PartyUsersList: React.FC = () => {
  const { partyUsers } = useChat();
  
  return (
    <div className="mb-4">
      <div className="flex items-center gap-2 mb-2">
        <Users size={16} className="text-lofi-purple" />
        <span className="text-sm font-medium">Party Members ({partyUsers.length})</span>
      </div>
      <div className="flex flex-wrap gap-2">
        {partyUsers.map(user => (
          <Badge key={user.id} variant="outline" className="bg-black/30">
            {user.username}
          </Badge>
        ))}
      </div>
    </div>
  );
};

// Create Party Dialog component
export const CreatePartyDialog: React.FC = () => {
  const { createParty } = useChat();
  const { toast } = useToast();
  const [isOpen, setIsOpen] = useState(false);
  
  const handleCreateParty = () => {
    createParty();
    setIsOpen(false);
    toast({
      title: 'Party Created',
      description: 'Your listening party has been created!',
    });
  };
  
  return (
    <Dialog open={isOpen} onOpenChange={setIsOpen}>
      <DialogTrigger asChild>
        <Button className="gap-2" variant="default">
          <Plus size={16} />
          Create Party
        </Button>
      </DialogTrigger>
      <DialogContent className="bg-black/90 border border-lofi-purple/30 text-white">
        <DialogHeader>
          <DialogTitle className="text-lofi-purple">Create a Listening Party</DialogTitle>
        </DialogHeader>
        <div className="py-4">
          <p className="mb-4">Create a new listening party to chat with friends while enjoying lofi beats together.</p>
          <Button onClick={handleCreateParty} className="w-full">
            Create Party
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
};

// Join Party Dialog component
export const JoinPartyDialog: React.FC = () => {
  const { joinParty } = useChat();
  const { toast } = useToast();
  const [partyId, setPartyId] = useState('');
  const [isOpen, setIsOpen] = useState(false);
  
  const handleJoinParty = () => {
    if (!partyId.trim()) {
      toast({
        title: 'Error',
        description: 'Please enter a valid party ID',
        variant: 'destructive',
      });
      return;
    }
    
    joinParty(partyId.trim().toUpperCase());
    setIsOpen(false);
    toast({
      title: 'Party Joined',
      description: 'You have joined the listening party!',
    });
  };
  
  return (
    <Dialog open={isOpen} onOpenChange={setIsOpen}>
      <DialogTrigger asChild>
        <Button className="gap-2" variant="outline">
          Join Party
        </Button>
      </DialogTrigger>
      <DialogContent className="bg-black/90 border border-lofi-purple/30 text-white">
        <DialogHeader>
          <DialogTitle className="text-lofi-purple">Join a Listening Party</DialogTitle>
        </DialogHeader>
        <div className="py-4 space-y-4">
          <p>Enter the party ID to join a listening party with friends.</p>
          <Input
            value={partyId}
            onChange={(e) => setPartyId(e.target.value)}
            placeholder="Enter Party ID (e.g., ABC123)"
            className="bg-black/50 border-lofi-purple/30"
          />
          <Button onClick={handleJoinParty} className="w-full">
            Join Party
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
};

// Party Info component
export const PartyInfo: React.FC = () => {
  const { currentParty, leaveParty } = useChat();
  const { toast } = useToast();
  
  if (!currentParty) return null;
  
  const copyPartyId = () => {
    navigator.clipboard.writeText(currentParty);
    toast({
      title: 'Copied!',
      description: 'Party ID copied to clipboard',
    });
  };
  
  return (
    <div className="flex items-center justify-between mb-4 bg-black/30 p-2 rounded-md">
      <div className="flex items-center gap-2">
        <span className="text-sm font-medium">Party ID:</span>
        <Badge variant="outline" className="bg-black/50 cursor-pointer" onClick={copyPartyId}>
          {currentParty}
          <Copy size={12} className="ml-1" />
        </Badge>
      </div>
      <Button variant="ghost" size="sm" onClick={leaveParty} className="h-8 px-2">
        <X size={16} className="text-red-400" />
        Leave
      </Button>
    </div>
  );
};

// Main Chat Panel component
export const ChatPanel: React.FC = () => {
  const { currentParty, messages, sendMessage, partyUsers } = useChat();
  const { user } = useAuth();
  const [newMessage, setNewMessage] = useState('');
  const messagesEndRef = useRef<HTMLDivElement>(null);
  
  // Scroll to bottom when new messages arrive
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);
  
  const handleSendMessage = (e: React.FormEvent) => {
    e.preventDefault();
    if (newMessage.trim()) {
      sendMessage(newMessage);
      setNewMessage('');
    }
  };
  
  if (!currentParty) {
    return (
      <Card className="bg-black/70 border border-lofi-purple/30 text-white h-full">
        <CardHeader>
          <CardTitle className="text-lofi-purple">Listening Party</CardTitle>
        </CardHeader>
        <CardContent className="flex flex-col items-center justify-center h-64 gap-4">
          <p className="text-center text-gray-400">
            Create or join a listening party to chat with friends while enjoying lofi beats together.
          </p>
          <div className="flex gap-2 mt-4">
            <CreatePartyDialog />
            <JoinPartyDialog />
          </div>
        </CardContent>
      </Card>
    );
  }
  
  return (
    <Card className="bg-black/70 border border-lofi-purple/30 text-white h-full flex flex-col">
      <CardHeader className="pb-2">
        <CardTitle className="text-lofi-purple flex items-center justify-between">
          <span>Listening Party</span>
          <Badge variant="outline" className="ml-2 font-normal text-xs">
            {partyUsers.length} {partyUsers.length === 1 ? 'member' : 'members'}
          </Badge>
        </CardTitle>
        <PartyInfo />
        <PartyUsersList />
      </CardHeader>
      <CardContent className="flex-grow overflow-hidden p-0 px-4">
        <ScrollArea className="h-[300px] pr-4">
          <div className="space-y-2 pt-2">
            {messages.length === 0 ? (
              <div className="text-center text-gray-400 py-8">
                No messages yet. Start the conversation!
              </div>
            ) : (
              messages.map((msg) => (
                <ChatMessage 
                  key={msg.id} 
                  message={msg} 
                  isCurrentUser={user?.id === msg.userId}
                />
              ))
            )}
            <div ref={messagesEndRef} />
          </div>
        </ScrollArea>
      </CardContent>
      <CardFooter className="pt-2">
        <form onSubmit={handleSendMessage} className="flex w-full gap-2">
          <Input
            value={newMessage}
            onChange={(e) => setNewMessage(e.target.value)}
            placeholder="Type a message..."
            className="flex-grow bg-black/50 border-lofi-purple/30"
          />
          <Button type="submit" size="icon" disabled={!newMessage.trim()}>
            <Send size={16} />
          </Button>
        </form>
      </CardFooter>
    </Card>
  );
};