import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { io, Socket } from 'socket.io-client';
import { useAuth } from './AuthContext';
import { ChatMessage, ChatUser } from '@/types';

interface ChatContextType {
  socket: Socket | null;
  isConnected: boolean;
  currentParty: string | null;
  messages: ChatMessage[];
  partyUsers: ChatUser[];
  joinParty: (partyId: string) => void;
  leaveParty: () => void;
  createParty: () => void;
  sendMessage: (message: string) => void;
}

const ChatContext = createContext<ChatContextType | undefined>(undefined);

export const useChat = () => {
  const context = useContext(ChatContext);
  if (context === undefined) {
    throw new Error('useChat must be used within a ChatProvider');
  }
  return context;
};

interface ChatProviderProps {
  children: ReactNode;
}

export const ChatProvider: React.FC<ChatProviderProps> = ({ children }) => {
  const [socket, setSocket] = useState<Socket | null>(null);
  const [isConnected, setIsConnected] = useState(false);
  const [currentParty, setCurrentParty] = useState<string | null>(null);
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [partyUsers, setPartyUsers] = useState<ChatUser[]>([]);
  const { user } = useAuth();

  // Initialize socket connection
  useEffect(() => {
    // Connect to the dedicated chat server
    const newSocket = io('http://localhost:3001');
    setSocket(newSocket);

    newSocket.on('connect', () => {
      setIsConnected(true);
    });

    newSocket.on('disconnect', () => {
      setIsConnected(false);
    });

    // Clean up on unmount
    return () => {
      newSocket.disconnect();
    };
  }, []);

  // Set up socket event listeners
  useEffect(() => {
    if (!socket) return;

    // Handle receiving messages
    socket.on('receive_message', (message: ChatMessage) => {
      setMessages((prev) => [...prev, message]);
    });

    // Handle user joining the party
    socket.on('user_joined', ({ users }: { users: ChatUser[] }) => {
      setPartyUsers(users);
    });

    // Handle user leaving the party
    socket.on('user_left', ({ users }: { users: ChatUser[] }) => {
      setPartyUsers(users);
    });

    // Handle party history when joining
    socket.on('party_history', ({ messages }: { messages: ChatMessage[] }) => {
      setMessages(messages);
    });

    // Handle party creation
    socket.on('party_created', ({ partyId }: { partyId: string }) => {
      setCurrentParty(partyId);
      joinParty(partyId);
    });

    return () => {
      socket.off('receive_message');
      socket.off('user_joined');
      socket.off('user_left');
      socket.off('party_history');
      socket.off('party_created');
    };
  }, [socket]);

  // Join a party
  const joinParty = (partyId: string) => {
    if (!socket || !user) return;
    
    setCurrentParty(partyId);
    socket.emit('join_party', {
      partyId,
      username: user.username,
    });
  };

  // Leave the current party
  const leaveParty = () => {
    if (!socket || !currentParty) return;
    
    socket.emit('leave_party', { partyId: currentParty });
    setCurrentParty(null);
    setMessages([]);
    setPartyUsers([]);
  };

  // Create a new party
  const createParty = () => {
    if (!socket || !user) return;
    
    socket.emit('create_party', { username: user.username });
  };

  // Send a message to the current party
  const sendMessage = (message: string) => {
    if (!socket || !currentParty || !message.trim()) return;
    
    socket.emit('send_message', {
      partyId: currentParty,
      message,
    });
  };

  return (
    <ChatContext.Provider
      value={{
        socket,
        isConnected,
        currentParty,
        messages,
        partyUsers,
        joinParty,
        leaveParty,
        createParty,
        sendMessage,
      }}
    >
      {children}
    </ChatContext.Provider>
  );
};