
import React from "react";
import { cn } from "@/lib/utils";
import { Station } from "@/types";
import { Card, CardContent } from "@/components/ui/card";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Button } from "@/components/ui/button";
import { Heart } from "lucide-react";
import { useAuth } from "@/contexts/AuthContext";
import { useToast } from "@/components/ui/use-toast";

interface StationPickerProps {
  stations: Station[];
  currentStationId: string | null;
  onStationSelect: (stationId: string) => void;
  className?: string;
}

export const StationPicker: React.FC<StationPickerProps> = ({
  stations,
  currentStationId,
  onStationSelect,
  className,
}) => {
  const { isAuthenticated, isFavorite, addFavorite, removeFavorite } = useAuth();
  const { toast } = useToast();

  const handleToggleFavorite = async (stationId: string) => {
    if (!isAuthenticated) {
      toast({
        title: "Authentication required",
        description: "Please login to save favorite stations",
        variant: "destructive",
      });
      return;
    }

    try {
      if (isFavorite(stationId)) {
        await removeFavorite(stationId);
        toast({
          title: "Removed from favorites",
          description: "Station removed from your favorites",
        });
      } else {
        await addFavorite(stationId);
        toast({
          title: "Added to favorites",
          description: "Station added to your favorites",
        });
      }
    } catch (error) {
      toast({
        title: "Error",
        description: "Failed to update favorites",
        variant: "destructive",
      });
    }
  };

  return (
    <div className={cn("bg-black/70 rounded-lg p-4 backdrop-blur-md", className)}>
      <h3 className="text-white font-pixel text-xl mb-4">Choose Your Vibe</h3>
      <ScrollArea className="h-[220px] pr-4">
        <div className="flex flex-col gap-3">
          {stations.map((station) => (
            <StationCard
              key={station.id}
              station={station}
              isActive={station.id === currentStationId}
              onClick={() => onStationSelect(station.id)}
              isFavorite={isFavorite(station.id)}
              onToggleFavorite={handleToggleFavorite}
            />
          ))}
        </div>
      </ScrollArea>
    </div>
  );
};

interface StationCardProps {
  station: Station;
  isActive: boolean;
  onClick: () => void;
  isFavorite: boolean;
  onToggleFavorite: (stationId: string) => void;
}

const StationCard: React.FC<StationCardProps> = ({ 
  station, 
  isActive, 
  onClick, 
  isFavorite, 
  onToggleFavorite 
}) => {
  const cardStyle = {
    borderColor: isActive ? station.color : "transparent",
    backgroundColor: isActive ? `${station.color}20` : "rgba(30, 30, 30, 0.6)",
  };

  const handleFavoriteClick = (e: React.MouseEvent) => {
    e.stopPropagation();
    onToggleFavorite(station.id);
  };

  return (
    <Card
      className={cn(
        "border hover:border-primary/70 transition-all cursor-pointer",
        isActive ? "ring-1 ring-offset-0" : ""
      )}
      style={cardStyle}
      onClick={onClick}
    >
      <CardContent className="p-3 flex items-center gap-3">
        <div className="w-12 h-12 rounded overflow-hidden flex-shrink-0">
          <img
            src={station.backgroundImage}
            alt={station.name}
            className="w-full h-full object-cover pixelated"
          />
        </div>
        <div className="flex-1">
          <h4 className="font-pixel text-white text-sm">{station.name}</h4>
          <p className="text-xs text-gray-400 font-pixel">{station.mood}</p>
        </div>
        <Button 
          variant="ghost" 
          size="icon" 
          className="h-8 w-8" 
          onClick={handleFavoriteClick}
        >
          <Heart 
            className={cn(
              "h-4 w-4", 
              isFavorite ? "fill-current text-red-500" : "text-gray-400"
            )} 
          />
        </Button>
      </CardContent>
    </Card>
  );
};
