import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { Station } from '../types';

interface User {
  id: string;
  username: string;
  email: string;
  favoriteStations: string[];
}

interface AuthContextType {
  user: User | null;
  isAuthenticated: boolean;
  isLoading: boolean;
  login: (username: string, password: string) => Promise<void>;
  register: (username: string, email: string, password: string) => Promise<void>;
  logout: () => void;
  addFavorite: (stationId: string) => Promise<void>;
  removeFavorite: (stationId: string) => Promise<void>;
  getFavorites: () => Promise<string[]>;
  isFavorite: (stationId: string) => boolean;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

interface AuthProviderProps {
  children: ReactNode;
}

export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // Check if user is logged in on initial load
    const checkAuth = async () => {
      const token = localStorage.getItem('token');
      if (token) {
        try {
          const response = await fetch('http://localhost:3000/api/users/me', {
            headers: {
              'x-auth-token': token
            }
          });
          
          if (response.ok) {
            const userData = await response.json();
            setUser(userData);
          } else {
            // Token invalid or expired
            localStorage.removeItem('token');
          }
        } catch (error) {
          console.error('Auth check error:', error);
        }
      }
      setIsLoading(false);
    };
    
    checkAuth();
  }, []);

  const login = async (username: string, password: string) => {
    try {
      const response = await fetch('http://localhost:3000/api/users/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ username, password })
      });
      
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Login failed');
      }
      
      const data = await response.json();
      localStorage.setItem('token', data.token);
      setUser(data.user);
    } catch (error) {
      console.error('Login error:', error);
      throw error;
    }
  };

  const register = async (username: string, email: string, password: string) => {
    try {
      const response = await fetch('http://localhost:3000/api/users/register', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ username, email, password })
      });
      
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Registration failed');
      }
      
      const data = await response.json();
      localStorage.setItem('token', data.token);
      setUser(data.user);
    } catch (error) {
      console.error('Registration error:', error);
      throw error;
    }
  };

  const logout = () => {
    localStorage.removeItem('token');
    setUser(null);
  };

  const addFavorite = async (stationId: string) => {
    try {
      const token = localStorage.getItem('token');
      if (!token || !user) throw new Error('Not authenticated');
      
      const response = await fetch('http://localhost:3000/api/favorites', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'x-auth-token': token
        },
        body: JSON.stringify({ stationId })
      });
      
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Failed to add favorite');
      }
      
      const favoriteStations = await response.json();
      setUser({ ...user, favoriteStations });
    } catch (error) {
      console.error('Add favorite error:', error);
      throw error;
    }
  };

  const removeFavorite = async (stationId: string) => {
    try {
      const token = localStorage.getItem('token');
      if (!token || !user) throw new Error('Not authenticated');
      
      const response = await fetch(`http://localhost:3000/api/favorites/${stationId}`, {
        method: 'DELETE',
        headers: {
          'x-auth-token': token
        }
      });
      
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Failed to remove favorite');
      }
      
      const favoriteStations = await response.json();
      setUser({ ...user, favoriteStations });
    } catch (error) {
      console.error('Remove favorite error:', error);
      throw error;
    }
  };

  const getFavorites = async (): Promise<string[]> => {
    try {
      const token = localStorage.getItem('token');
      if (!token) return [];
      
      const response = await fetch('http://localhost:3000/api/favorites', {
        headers: {
          'x-auth-token': token
        }
      });
      
      if (!response.ok) {
        return [];
      }
      
      return await response.json();
    } catch (error) {
      console.error('Get favorites error:', error);
      return [];
    }
  };

  const isFavorite = (stationId: string): boolean => {
    return user?.favoriteStations.includes(stationId) || false;
  };

  const value = {
    user,
    isAuthenticated: !!user,
    isLoading,
    login,
    register,
    logout,
    addFavorite,
    removeFavorite,
    getFavorites,
    isFavorite
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};